.testflight-page .advantage {
  color: #3b566e;
  text-align: center; }
  .testflight-page .advantage h1 {
    margin-bottom: 40px; }
  .testflight-page .advantage .img {
    display: block;
    width: 180px;
    height: 180px;
    margin: 0 auto; }
  .testflight-page .advantage .img-01 {
    background: url(./../../images/testflight/home-03.png) no-repeat center center;
    background-size: contain; }
  .testflight-page .advantage .img-02 {
    background: url(./../../images/testflight/home-04.png) no-repeat center center;
    background-size: contain; }
  .testflight-page .advantage .img-03 {
    background: url(./../../images/testflight/home-05.png) no-repeat center center;
    background-size: contain; }
  .testflight-page .advantage .img-04 {
    background: url(./../../images/testflight/home-06.png) no-repeat center center;
    background-size: contain; }
  .testflight-page .advantage h2 {
    margin-top: 20px;
    line-height: 40px;
    font-weight: 400; }
  .testflight-page .advantage p {
    font-size: 16px;
    line-height: 34px;
    margin-top: 20px; }

.testflight-page .certificate {
  padding: 90px 0;
  background: #f7fafc;
  overflow: hidden; }
  .testflight-page .certificate img {
    height: 502px;
    display: block;
    margin: 0 auto; }
  .testflight-page .certificate h1 {
    font-weight: 500;
    font-size: 24px;
    color: #3B566E;
    margin-top: 120px;
    margin-bottom: 20px; }
  .testflight-page .certificate h2 {
    font-weight: 500;
    font-size: 20px;
    color: #3B566E; }
  .testflight-page .certificate p {
    font-size: 15px;
    font-weight: 500;
    color: #6f8ba4; }
